@use "sass:map";
@use "colors" as *;
@use "../../settings" as *;
@use "../../utils/box-shadow";
@use "../../utils/functions";

// Default: Light theme
@mixin theme {
  #{$âœ¨}background-color: #{$white};

  // Text color
  #{$âœ¨}color: #{$grey-700};

  // Muted colors
  #{$âœ¨}muted-color: #{$grey-500};
  #{$âœ¨}muted-border-color: #{$grey-50};

  // Primary colors
  #{$âœ¨}primary: #{$primary-600};
  #{$âœ¨}primary-hover: #{$primary-700};
  #{$âœ¨}primary-focus: #{rgba($primary-600, 0.125)};
  #{$âœ¨}primary-inverse: #{$white};

  // Secondary colors
  #{$âœ¨}secondary: #{$grey-600};
  #{$âœ¨}secondary-hover: #{$grey-700};
  #{$âœ¨}secondary-focus: #{rgba($grey-600, 0.125)};
  #{$âœ¨}secondary-inverse: #{$white};

  // Contrast colors
  #{$âœ¨}contrast: #{$grey-900};
  #{$âœ¨}contrast-hover: #{$black};
  #{$âœ¨}contrast-focus: #{rgba($grey-600, 0.125)};
  #{$âœ¨}contrast-inverse: #{$white};

  // Box shadow
  #{$âœ¨}box-shadow: box-shadow.shadow($grey-900);

  // Typography
  @if map.get($modules, "content/typography") {
    // Headings colors
    #{$âœ¨}h1-color: #{$grey-900};
    #{$âœ¨}h2-color: #{mix($grey-900, $grey-800)};
    #{$âœ¨}h3-color: #{$grey-800};
    #{$âœ¨}h4-color: #{mix($grey-800, $grey-700)};
    #{$âœ¨}h5-color: #{$grey-700};
    #{$âœ¨}h6-color: #{mix($grey-700, $grey-600)};

    // Highlighted text (<mark>)
    #{$âœ¨}mark-background-color: #{mix($amber-100, $amber-50)};
    #{$âœ¨}mark-color: #{mix($grey-900, $amber-900, 75%)};

    // Inserted (<ins>) & Deleted (<ins>)
    #{$âœ¨}ins-color: #{$green-700};
    #{$âœ¨}del-color: #{$red-800};

    // Blockquote
    #{$âœ¨}blockquote-border-color: var(#{$âœ¨}muted-border-color);
    #{$âœ¨}blockquote-footer-color: var(#{$âœ¨}muted-color);
  }

  // Button
  @if map.get($modules, "content/button") {
    // To disable box-shadow, remove the var or set to '0 0 0 rgba(0, 0, 0, 0)'
    // Don't use, 'none, 'false, 'null', '0', etc.
    #{$âœ¨}button-box-shadow: 0 0 0 rgba(0, 0, 0, 0);
    #{$âœ¨}button-hover-box-shadow: 0 0 0 rgba(0, 0, 0, 0);
  }

  // Table
  @if map.get($modules, "content/table") {
    #{$âœ¨}table-border-color: var(#{$âœ¨}muted-border-color);
    #{$âœ¨}table-row-stripped-background-color: #{mix($grey-50, $white)};
  }

  // Code
  @if map.get($modules, "content/code") {
    #{$âœ¨}code-background-color: #{$grey-50};
    #{$âœ¨}code-color: var(#{$âœ¨}muted-color);
    #{$âœ¨}code-kbd-background-color: var(#{$âœ¨}contrast);
    #{$âœ¨}code-kbd-color: var(#{$âœ¨}contrast-inverse);
    #{$âœ¨}code-tag-color: #{hsl(330, 40%, 50%)};
    #{$âœ¨}code-property-color: #{hsl(185, 40%, 40%)};
    #{$âœ¨}code-value-color: #{hsl(40, 20%, 50%)};
    #{$âœ¨}code-comment-color: #{$grey-300};
  }

  // Form elements
  @if map.get($modules, "forms/basics") {
    #{$âœ¨}form-element-background-color: transparent;
    #{$âœ¨}form-element-border-color: #{$grey-300};
    #{$âœ¨}form-element-color: var(#{$âœ¨}color);
    #{$âœ¨}form-element-placeholder-color: var(#{$âœ¨}muted-color);
    #{$âœ¨}form-element-active-background-color: transparent;
    #{$âœ¨}form-element-active-border-color: var(#{$âœ¨}primary);
    #{$âœ¨}form-element-focus-color: var(#{$âœ¨}primary-focus);
    #{$âœ¨}form-element-disabled-background-color: #{$grey-100};
    #{$âœ¨}form-element-disabled-border-color: #{$grey-300};
    #{$âœ¨}form-element-disabled-opacity: 0.5;
    #{$âœ¨}form-element-invalid-border-color: #{$red-800};
    #{$âœ¨}form-element-invalid-active-border-color: #{$red-700};
    #{$âœ¨}form-element-invalid-focus-color: #{rgba($red-700, 0.125)};
    #{$âœ¨}form-element-valid-border-color: #{$green-700};
    #{$âœ¨}form-element-valid-active-border-color: #{$green-600};
    #{$âœ¨}form-element-valid-focus-color: #{rgba($green-600, 0.125)};
  }

  // Switch (input[type="checkbox"][role="switch"])
  @if map.get($modules, "forms/checkbox-radio-switch") {
    #{$âœ¨}switch-background-color: #{$grey-200};
    #{$âœ¨}switch-color: var(#{$âœ¨}primary-inverse);
    #{$âœ¨}switch-checked-background-color: var(#{$âœ¨}primary);
  }

  // Range (input[type="range"])
  @if map.get($modules, "forms/input-range") {
    #{$âœ¨}range-border-color: #{$grey-100};
    #{$âœ¨}range-active-border-color: #{$grey-200};
    #{$âœ¨}range-thumb-border-color: var(#{$âœ¨}background-color);
    #{$âœ¨}range-thumb-color: var(#{$âœ¨}secondary);
    #{$âœ¨}range-thumb-hover-color: var(#{$âœ¨}secondary-hover);
    #{$âœ¨}range-thumb-active-color: var(#{$âœ¨}primary);
  }

  // Accordion (<details>)
  @if map.get($modules, "components/accordion") {
    #{$âœ¨}accordion-border-color: var(#{$âœ¨}muted-border-color);
    #{$âœ¨}accordion-active-summary-color: var(#{$âœ¨}primary);
    #{$âœ¨}accordion-close-summary-color: var(#{$âœ¨}color);
    #{$âœ¨}accordion-open-summary-color: var(#{$âœ¨}muted-color);
  }

  // Card (<article>)
  @if map.get($modules, "components/card") {
    #{$âœ¨}card-background-color: var(#{$âœ¨}background-color);
    #{$âœ¨}card-border-color: var(#{$âœ¨}muted-border-color);
    #{$âœ¨}card-box-shadow: var(#{$âœ¨}box-shadow);
    #{$âœ¨}card-sectionning-background-color: #{mix($grey-50, $white, 25%)};
  }

  // Dropdown (<details role="list">)
  @if map.get($modules, "components/dropdown") {
    #{$âœ¨}dropdown-background-color: #{mix($grey-50, $white, 25%)};
    #{$âœ¨}dropdown-border-color: #{mix($grey-100, $grey-50)};
