@use "sass:map";
@use "../../settings" as *;

@if map.get($modules, "themes/default") {
  /**
   * Styles
   */

  :root {
    // Typography
    #{$âœ¨}font-family: system-ui, -apple-system, "Segoe UI", "Roboto", "Ubuntu", "Cantarell",
      "Noto Sans", sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol",
      "Noto Color Emoji";
    #{$âœ¨}line-height: 1.5;
    #{$âœ¨}font-weight: 400;
    #{$âœ¨}font-size: 16px;

    // Responsive root font size
    @if $enable-responsive-typography {
      @each $key, $values in $breakpoints {
        @if $values {
          @media (min-width: map.get($values, "breakpoint")) {
            #{$âœ¨}font-size: map.get($values, "root-font-size");
          }
        }
      }
    }

    // Borders
    #{$âœ¨}border-radius: 0.25rem;
    #{$âœ¨}border-width: 1px;
    #{$âœ¨}outline-width: 3px;

    // Transitions
    #{$âœ¨}transition: 0.2s ease-in-out;

    // Spacings
    #{$âœ¨}spacing: 1rem;

    // Spacings for typography elements
    @if map.get($modules, "content/typography") {
      #{$âœ¨}typography-spacing-vertical: 1.5rem;
    }

    // Spacings for body > header, body > main, body > footer, section, article
    @if map.get($modules, "layout/landmarks") or
      map.get($modules, "layout/section") or
      map.get($modules, "components/card") or
      map.get($modules, "components/modal")
    {
      #{$âœ¨}block-spacing-vertical: calc(var(#{$âœ¨}spacing) * 2);
      #{$âœ¨}block-spacing-horizontal: var(#{$âœ¨}spacing);
    }

    @if map.get($modules, "layout/grid") and $enable-classes {
      #{$âœ¨}grid-spacing-vertical: 0;
      #{$âœ¨}grid-spacing-horizontal: var(#{$âœ¨}spacing);
    }

    // Spacings for form elements and button
    @if map.get($modules, "content/button") or map.get($modules, "forms/basic") {
      #{$âœ¨}form-element-spacing-vertical: 0.75rem;
      #{$âœ¨}form-element-spacing-horizontal: 1rem;
    }

    // Font weight for form labels & fieldsets legend
    @if map.get($modules, "forms/basic") {
      #{$âœ¨}form-label-font-weight: var(#{$âœ¨}font-weight);
    }

    // Modal (<dialog>)
    @if map.get($modules, "components/modal") {
      #{$âœ¨}modal-overlay-backdrop-filter: blur(0.25rem);
    }

    // Spacings for nav component
    @if map.get($modules, "components/nav") {
      #{$âœ¨}nav-element-spacing-vertical: 1rem;
      #{$âœ¨}nav-element-spacing-horizontal: 0.5rem;
      #{$âœ¨}nav-link-spacing-vertical: 0.5rem;
      #{$âœ¨}nav-link-spacing-horizontal: 0.5rem;
    }
  }

  // Responsives spacings
  @if $enable-responsive-spacings {
    // Landmarks and section
    @if map.get($modules, "layout/landmarks") or map.get($modules, "layout/section") {
      #{$semantic-root-element} > header,
      #{$semantic-root-element} > main,
      #{$semantic-root-element} > footer,
      section {
        @each $key, $values in $breakpoints {
          @if $values {
            @media (min-width: map.get($values, "breakpoint")) {
              $multiplier: 1;
              @if $key == "sm" {
                $multiplier: 2.5;
              } @else if $key == "md" {
                $multiplier: 3;
              } @else if $key == "lg" {
                $multiplier: 3.5;
              } @else if $key == "xl" {
                $multiplier: 4;
              }
              #{$âœ¨}block-spacing-vertical: calc(var(#{$âœ¨}spacing) * $multiplier);
            }
          }
        }
      }
    }

    // Card (<article>)
    @if map.get($modules, "components/card") {
      article {
        @each $key, $values in $breakpoints {
          @if $values {
            @media (min-width: map.get($values, "breakpoint")) {
              $multiplier: 1;
              @if $key == "sm" {
                $multiplier: 1.25;
              } @else if $key == "md" {
                $multiplier: 1.5;
              } @else if $key == "lg" {
                $multiplier: 1.75;
              } @else if $key == "xl" {
                $multiplier: 2;
              }
              #{$âœ¨}block-spacing-horizontal: calc(var(#{$âœ¨}spacing) * $multiplier);
            }
          }
        }
      }
    }

    // Modal
    @if map.get($modules, "components/modal") {
      dialog > article {
        #{$âœ¨}block-spacing-vertical: calc(var(#{$âœ¨}spacing) * 2);
        #{$âœ¨}block-spacing-horizontal: var(#{$âœ¨}spacing);

        @if map.get($breakpoints, "sm") {
          @media (min-width: map.get(map.get($breakpoints, "sm"), "breakpoint")) {
            #{$âœ¨}block-spacing-vertical: calc(var(#{$âœ¨}spacing) * 2.5);
            #{$âœ¨}block-spacing-horizontal: calc(var(#{$âœ¨}spacing) * 1.25);
          }
        }

        @if map.get($breakpoints, "md") {
          @media (min-width: map.get(map.get($breakpoints, "md"), "breakpoint")) {
            #{$âœ¨}block-spacing-vertical: calc(var(#{$âœ¨}spacing) * 3);
            #{$âœ¨}block-spacing-horizontal: calc(var(#{$âœ¨}spacing) * 1.5);
          }
        }
      }
    }
  }

  // Link
  @if map.get($modules, "content/link") {
    a {
      #{$âœ¨}text-decoration: none;

      // Secondary & Contrast
      @if enable-classes {
        &.secondary,
        &.contrast {
          #{$âœ¨}text-decoration: underline;
        }
      }
    }
  }

  // Typography
  @if map.get($modules, "content/typography") {
    // Small
    small {
      #{$âœ¨}font-size: 0.875em;
    }

    // Headings
    h1,
    h2,
    h3,
    h4,
    h5,
    h6 {
      #{$âœ¨}font-weight: 700;
    }

    h1 {
      #{$âœ¨}font-size: 2rem;
      #{$âœ¨}typography-spacing-vertical: 3rem;
    }

    h2 {
      #{$âœ¨}font-size: 1.75rem;
      #{$âœ¨}typography-spacing-vertical: 2.625rem;
    }

    h3 {
      #{$âœ¨}font-size: 1.5rem;
      #{$âœ¨}typography-spacing-vertical: 2.25rem;
    }

    h4 {
      #{$âœ¨}font-size: 1.25rem;
      #{$âœ¨}typography-spacing-vertical: 1.874rem;
    }

    h5 {
      #{$âœ¨}font-size: 1.125rem;
      #{$âœ¨}typography-spacing-vertical: 1.6875rem;
    }
  }

  // Table
  @if map.get($modules, "content/table") {
    thead,
    tfoot {
      th,
      td {
        #{$âœ¨}border-width: 3px;
      }
    }

    :not(thead, tfoot) > * > td {
      #{$âœ¨}font-size: 0.875em;
    }
  }

  // Code
  @if map.get($modules, "content/code") {
    pre,
    code,
    kbd,
    samp {
      #{$âœ¨}font-family: "Menlo", "Consolas", "Roboto Mono", "Ubuntu Monospace", "Noto Mono",
        "Oxygen Mono", "Liberation Mono", monospace, "Apple Color Emoji", "Segoe UI Emoji",
        "Segoe UI Symbol", "Noto Color Emoji";
    }

    kbd {
      #{$âœ¨}font-weight: bolder;
    }
  }

  // Checkboxes, Radios and Switches
  @if map.get($modules, "forms/checkbox-radio-switch") {
    [type="checkbox"],
    [type="radio"] {
      #{$âœ¨}border-width: 2px;
    }

    [type="checkbox"][role="switch"] {
      #{$âœ¨}border-width: 3px;
    }
  }
}
